import uniFab from '@/components/uni-fab/uni-fab.vue';
export default {
	components: {
		uniFab
	},
	data() {
		return {
			fujinOrderList:[
				{}
			],
			xuanzhuan:0,
			orderList: [
				{
					orderStatus1: '已接订单',
					orderAddress: '广州番禺南国奥园加分加来到了金发的链',
					orderMoney: 43,
					orderStatus2: '洗车中'
				}
			],
			horizontal: 'right',
			vertical: 'bottom',
			direction: 'vertical',
			pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#007AFF',
				buttonColor: '#007AFF'
			},
			content: [
				{
					iconPath: '/static/img/user_center.png',
					selectedIconPath: '/static/img/user_center.png',
					text: '个人中心',
					active: false
				},
				{
					iconPath: '/static/img/notice_center.png',
					selectedIconPath: '/static/img/notice_center.png',
					text: '消息中心',
					active: false
				}
			]
		};
	},

	onLoad() {},
	methods: {
		doXuanzhuan(){
			this.xuanzhuan += 360;
		},
		trigger(e) {
			console.log(e);
			this.content[e.index].active = !e.item.active;
			if (e.index - 0 == 0) {
				this.naviagetorToPages('/pages/user/user');
			}
			if (e.index - 1 == 0) {
				this.naviagetorToPages('/pages/notice/notice');
			}
		},
		naviagetorToPages(url) {
			uni.navigateTo({
				url: url
			});
		}
	}
};
